.App {
  text-align: center;
  background-color: #CE3333;
  height: 900px;
}

.App-logo {
  -webkit-animation: fontbulger infinite 5s linear;
          animation: fontbulger infinite 5s linear;
  height: 80px;
}

.goButton{
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; 
}


.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}
@-webkit-keyframes fontbulger {
  0%, 100% {
    font-size: 10px;
  }
  50% {
    font-size: 12px;
  }
}
@keyframes fontbulger {
  0%, 100% {
    font-size: 10px;
  }
  50% {
    font-size: 12px;
  }
}



